.container {
  max-width: 800px;
  border-radius: 5px;
  margin: 50px auto;
  padding: 20px;
  box-shadow: 0 2px 10px rgba(0, 0, 0, .1);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  background: #fff;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 2px 10px rgba(0, 0, 0, .1);
}

.image-container img {
  width: 100%;
  border-radius: 5px;
}

.cat-image {
  width: 300px;
  height: auto;
  margin-right: 20px;
}

.cat-details {
  flex: 1;
}

.breed-select {
  width: 200px;
  height: 40px;
  border-radius: 4px;
  padding: 7px;
  font-size: large;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .1);
}

.loader {
  width: 50px;
  height: 50px;
  color: rgba(0, 0, 0, 0);
  border: 4px solid #ccc;
  border-top-color: #333;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.error {
  color: #fff;
  background: #f44336;
  border-radius: 4px;
  padding: 10px 15px;
}

.info-container {
  background: rgba(0, 255, 34, .12);
  border: 3px solid rgba(102, 102, 102, .12);
  border-radius: 10px;
  padding: 15px;
}

